





























































































































.menu-item {
  max-width: 4.25rem;
  height: 2.875rem;
}
.label-menu {
  font-size: 10px;
}
.section-status {
  right: 0;
  left: 0;
  width: 14px;
  height: 14px;
  margin: 0 auto;
  font-size: 8px;
  transform: translateX(50%);
}
.list-demo-enter {
  opacity: 0;
  transform: translateX(-50px);
}
.list-demo-enter-active {
  transition: all 0.3s 0.3s ease-out;
}
.list-demo-leave-active {
  position: absolute;
}
.list-demo-leave-to {
  opacity: 0;
}
.list-demo-move {
  transition: transform 0.3s 0s ease-in;
}
